                format: '@',    //  date format
                timePicker: '@', //boolean value if date with time picker
                isValueDateObj: '@',
                readonly: '@',
                disable: '@',
                prefixicon: '@',
                suffixicon: '@',
                maxlength: '@',
                min: '@',
                max: '@',
                timePicker12Hour: '@',
                minuteIncrement: '@',
                charactercounter: '@',
                decimalprecision: '@',
                minmaxprecision: '@',
