            scope.dateFormat = moment.localeData().longDateFormat('L');
            if (scope.timePicker == true) {
                scope.dateFormat = moment.localeData().longDateFormat('L') + ' ' + moment.localeData().longDateFormat('LT');
            }
            scope.format = scope.dateFormat;
